* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 17px;
}

@media (max-width: 900px) {
  html {
    font-size: 15px;
  }
}
@media (max-width: 400px) {
  html {
    font-size: 13px;
  }
}

@keyframes homeAnim {
  from {opacity: 0;}
  to {opacity:1;}
}

@keyframes contAnim {
  from {opacity: 0;}
  to {opacity:1;}
}


/* button animation */

@import url('https://fonts.googleapis.com/css?family=Lato:100&display=swap');
.center {
  width: 180px;
  height: 60px;
  position:Relative;
  top:50px;
}

.btn {
  width: 180px;
  height: 60px;
  cursor: pointer;
  background: black;
  border:1px solid #000;
  outline: none;
  transition: 1s ease-in-out;
}

svg {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

.btn:hover {
  transition: 1s ease-in-out;
  background:#fff;
}

.btn:hover svg {
  stroke-dashoffset: -480;
}
.btn {
  color: #fff;
  font-size: 18px;
  font-weight: 100;
}
.btn:hover {
  color:#000;
}



